import EventEmitter from 'events';
class sizeStore extends EventEmitter{
    constructor(){
        super(...arguments);
        this.winSize={
            width:window.innerWidth,
            height:window.innerHeight
        };
        this.topBox={
            height:50
        };
        this.leftBox={
            _swtich:[0,200],
            width:200
        };
        this.contentBox={};
    }
    getWinSize(){
        return this.winSize
    }
    getLeftBoxCss(){
        const _this=this;
        return {
            width:_this.leftBox.width+'px',
            height:(_this.winSize.height-_this.topBox.height)+'px'
        }
    }
    getLeftBoxState(){
        return this.leftBox._swtich[0]===this.leftBox.width;
    }
    getContentBoxCss(){
        const _this=this;
        return {
            width:(_this.winSize.width-_this.leftBox.width)+'px',
            height:(_this.winSize.height-_this.topBox.height)+'px'
        }
    }
    leftBoxWidthSwtich(){
        const _swtich=this.leftBox._swtich;
        const width=(this.leftBox.width===_swtich[0]&&_swtich[1]||_swtich[0]);
        this.leftBox.width=width;
        this.contentBox.width=this.winSize.width-width;
    }
    setLeftBoxWidth(width){
        width=width||this.leftBox.width;
        this.leftBox.width=width;
        this.contentBox.width=this.winSize.width-width
    }
    winSizeChange(size){
        size=size||{width:window.innerWidth,height:window.innerHeight};
        this.winSize=size;
        this.leftBox.height=size.height-this.topBox.height;
        this.contentBox.height=this.leftBox.height;
        this.contentBox.width=size.width-this.leftBox.width;
    }
    addListener(type,callback){
        this.on(type,callback);
    }
    remove(type,callback){
        this.removeListener(type,callback)
    }
    emitChange(type){
        this.emit(type)
    }

}
