import getLODOP from './getLODOP';
import Util from './util';
import config from './config';
class Dispatcher {
    static eventPool=[];
    static joiner='-';
    basePrefix=Util.getId();
    constructor(option){
        this.prefix=option.prefix;
        this.id=option.id||'';
    }
    on(type,callback,once,stop){
        return Dispatcher.on(type,this.id,callback,once,stop);
    }
    emit(type,data){
        return Dispatcher.emit(type,data,this.id,this);
    }
    off(type,callback){
        return Dispatcher.off(type,this.id,callback);
    }
    once(type,name,callback,stop){
        return Dispatcher.once(type,this.id,callback,stop);
    }
    onByObject(option,clipping=true){
        if(!Util.isObject(option))return false;
        for(let key in option){
            if(!option.hasOwnProperty(key)) continue;
            if(Util.isFunction(option[key])){
                this.on(key,option[key]);
                if(clipping){
                    delete option[key]
                }
            }else if(Util.isObject(option[key])){
                this.onByObject(option[key],clipping);
            }
        }
    }
    static once(type,name,callback,stop){
        return Dispatcher.on(type,name,callback,true,stop);
    }
    static on(type,name,callback,once=false,stop=true){
        let opt=type;
        const $events=Dispatcher.eventPool;
        if(typeof type==='string'){
            opt={};
            opt.type=type;
            if(typeof name ==='string'||Util.isFunction(callback)){
                opt.name=name;
                opt.callback=callback
            }else {
                opt.callback=name
            }
            opt.once=once;
            opt.stop=stop;
        }
        if(!Util.isFunction(opt.callback))return false;
        if(!opt.type||typeof opt.type!=='string')return false;
        opt.async=opt.type.split('!').length>1;
        for(let i=0,len=$events.length;i<len;i++){
            if(opt.name&&$events[i].name&&opt.name===$events[i].name&&opt.type===$events[i].type){
                return false
            }
        }
        $events.push(opt);
        return true
    }
    static emit(type,data,name,scope){
        scope=scope||this;
        const $events=Dispatcher.eventPool;
        let number=0;
        for(let i=0,len=$events.length;i<len;i++){
            let currentEvent=$events[i];
            if(type&&type===currentEvent.type){
                if(!(name&&currentEvent.name&&currentEvent.name!==name)){
                    number++;
                    currentEvent.callback.call(scope||{},data);
                    if(currentEvent.once===true){
                        setTimeout(()=>{Dispatcher.off(currentEvent.type,currentEvent.name,currentEvent.callback)},0)
                    }
                    if(currentEvent.stop===false&&currentEvent.name){
                        let eventNames=currentEvent.name.split('.');
                        if(eventNames.length>1){
                            for(let j=0;j<eventNames.length;j++){
                                let _eventNames=eventNames;
                                _eventNames.splice(eventNames.length-1-j,j+1);
                                if(Dispatcher.emit(type,data,_eventNames.join('.'),scope)!==0){
                                    break
                                }
                            }
                        }
                    }
                }
            }
        }
        return number;
    }
    static off(type,name,callback){
        let number=0;
        if(arguments.length===0){
            number=Dispatcher.eventPool.length;
            Dispatcher.eventPool=[];
            return number
        }
        let opt=type;
        const $events=Dispatcher.eventPool;
        if(typeof type==='string'){
            opt={};
            opt.type=type;
            if(typeof name ==='string'||callback){
                opt.name=name;
                opt.callback=callback
            }else {
                opt.callback=name
            }
        }
        for(let i=0;i<$events.length;i++){
            if(!(opt.name&&$events[i].name&&opt.name!==$events[i].name||(opt.type&&$events[i].type&&opt.type!==$events[i].type))){
                if(!opt.callback||opt.callback===$events[i].callback){
                    number++;
                    $events.splice(i,1);
                    i--;
                }
            }
        }
        Dispatcher.eventPool=$events;
        return number;
    }
    static buildId(prefix,id){
        return prefix+'.'+id
    }
    static publish(){

    }
}
class Action extends Dispatcher{
    static actions=[];

    /**
     * @constructor
     * @return {{status}} {Number}  0 1 2 3
     */
    constructor(){
        super(...arguments);
        const parameter=[...arguments];
        let option=null;
        if(!Util.isObject(parameter[0])){
            if(Util.isFunction(parameter[1])){
                parameter.splice(1,0,null);
            }
            option={
                type:parameter[0],
                name:parameter[1],
                callback:parameter[2]
            };
        }else {
            option=parameter[0]
        }
        Action.checkAndInit(option);

        this.id=Util.getId();
        this.startTime=false;
        this.endTime=false;
        this.status=0;
        this.type=option.type;
        this.name=option.name;
        this.callback=option.callback;
        this.methods=option.methods||null;

        this.onByObject(this.methods);
        Action.actions.push(this);
        this.emit('afterFilling');
    }
    static checkAndInit(option){
        const exampleTip=' (type,name,callback)||(type,callback)||({type:type,callback:callback}) ';
        if(!option.type||typeof option.type!=='string'){
            throw 'Action type'+exampleTip;
        }
        if(!Util.isFunction(option.callback)){
            throw 'Action callbackFunction,'+exampleTip;
        }
        if(typeof option.isOnce !=='boolean'){
            option.isOnce=true;
        }
        if(typeof option.isStop !=='boolean'){
            option.isStop=false;
        }
        if(typeof option.hasDependency !=='boolean'){
            option.hasDependency=false;
        }
        if(/[!]/.test(option.type)){
            option.async=true;
            option.params=option.type.split('!')[1]||null;
            option.type=option.type.split('!')[0];

        }
    }
}
class Print {
    static print(data){
        //
    }
}
class Main extends Dispatcher{
    constructor(option){
        const id=Util.getId();
        option.prefix=id;
        super(option);
        this.id=id;
    }
    push(option){
        if(!Util.isObject(option)){
            throw '';
        }
        if(!option.data){

        }
        return new Action(Object.assign({
            type:'print!',
            callback:Print.print,
            dependency:option.dependency,
            prefix:this.id,
            once:true,
            stop:false,
            runEnd:false,
            timeOut:null
        },option));
    }
    print(){
        this.emit('print');
    }
    console(){
        console.log(Action.actions);
    }
}
export default Main



