import sizeStore from './sizeStore';
import AppDispatcher from './AppDisoatcher';
AppDispatcher.register(function (action) {
    switch (action.actionType){
        case 'LEFT_BOX_WIDTH_SWTICH':
            sizeStore.leftBoxWidthSwtich();
            sizeStore.emitChange('LEFT_BOX_WIDTH_SWTICH');
    }
});
export default AppDispatcher



