import EventEmitter from 'events';
import assign from 'object-assign';
const NavStore=assign({},EventEmitter.prototype,{
    response:null,//
    navs:null,//
    secondNavs:null,//
    getServer(){
        return new Promise((resolve)=>{
            if(this.response){
                resolve(this.response);
            }else {
                const simulationData= `{
                    "errors": null,
                    "exception": null,
                    "model": [{
                        "active": "Y",
                        "createTime": null,
                        "createUserCode": null,
                        "createUserName": null,
                        "id": 60,
                        "moduleName": "failureOrderManagement",
                        "parentCode": "P2017041011255468400000103",
                        "path": "fdfg/adsf.action",
                        "perCode": "orderFaile:*",
                        "pimsType": "menu",
                        "pmisCode": "P2017042411190401900000002",
                        "pmisDescription": "1",
                        "pmisName": "",
                        "updateTime": null,
                        "updateUserCode": null,
                        "updateUserName": null
                    }, {
                        "active": "Y",
                        "createTime": null,
                        "createUserCode": null,
                        "createUserName": null,
                        "id": 32,
                        "moduleName": "personalInformationSet",
                        "parentCode": "P2017041011290433800000105",
                        "path": "/person/personInfo.action",
                        "perCode": "person:*",
                        "pimsType": "menu",
                        "pmisCode": "P2017041011333155600000110",
                        "pmisDescription": "",
                        "pmisName": "",
                        "updateTime": null,
                        "updateUserCode": null,
                        "updateUserName": null
                    }, {
                        "active": "Y",
                        "createTime": null,
                        "createUserCode": null,
                        "createUserName": null,
                        "id": 31,
                        "moduleName": "reciveSendPersonManage",
                        "parentCode": "P2017041011290433800000105",
                        "path": "/management/managementInfo.action",
                        "perCode": "management:*",
                        "pimsType": "menu",
                        "pmisCode": "P2017041011322163300000109",
                        "pmisDescription": "",
                        "pmisName": "",
                        "updateTime": null,
                        "updateUserCode": null,
                        "updateUserName": null
                    }, {
                        "active": "Y",
                        "createTime": null,
                        "createUserCode": null,
                        "createUserName": null,
                        "id": 27,
                        "moduleName": "",
                        "parentCode": "1",
                        "path": "",
                        "perCode": "system:*",
                        "pimsType": "menu",
                        "pmisCode": "P2017041011290433800000105",
                        "pmisDescription": "",
                        "pmisName": "",
                        "updateTime": null,
                        "updateUserCode": null,
                        "updateUserName": null
                    }, {
                        "active": "Y",
                        "createTime": null,
                        "createUserCode": null,
                        "createUserName": null,
                        "id": 26,
                        "moduleName": "orderManagement",
                        "parentCode": "P2017041011255468400000103",
                        "path": "order/advance.js",
                        "perCode": "order:orderList",
                        "pimsType": "menu",
                        "pmisCode": "P2017041011282378600000104",
                        "pmisDescription": "",
                        "pmisName": "",
                        "updateTime": null,
                        "updateUserCode": null,
                        "updateUserName": null
                    }, {
                        "active": "Y",
                        "createTime": null,
                        "createUserCode": null,
                        "createUserName": null,
                        "id": 25,
                        "moduleName": "",
                        "parentCode": "1",
                        "path": "",
                        "perCode": "order:*",
                        "pimsType": "menu",
                        "pmisCode": "P2017041011255468400000103",
                        "pmisDescription": "",
                        "pmisName": "",
                        "updateTime": null,
                        "updateUserCode": null,
                        "updateUserName": null
                    }],
                    "success": true
                }`;
                setTimeout(()=>{
                    this.response=JSON.parse(simulationData);
                    resolve(this.response)
                },100);
            }
        });
    },
    doResponse(){
        let getServer=this.getServer();
        return new Promise((resolve)=>{
            getServer.then((response)=>{
                const navs={};
                const second=[];
                response.model.forEach((val)=>{
                    if(!val.path||!val.parentCode){
                        navs[val.pmisName]=val;
                    }else {
                        second.push(val);
                    }
                });
                for(let key in navs){
                    navs[key].child=[];
                    second.forEach((val)=>{
                        if(val.parentCode==navs[key].pmisCode){
                            val.parentName=key;
                            navs[key].child.push(val);
                        }
                    })
                }
                const arr=[];
                for(let _key in navs){
                    arr.push(navs[_key]);
                }
                this.navs=arr;
                this.secondNavs=second;
                resolve(second)
            });
        })
    },
    getNav(){
        return new Promise((resolve=>{
            if(this.navs){
                resolve(this.navs);
            }else {
                let doResponse=this.doResponse();
                doResponse.then(()=>resolve(this.navs));
            }
        }))
    },
    getSecondNavs(){
        return new Promise((resolve=>{
            if(this.secondNavs){
                resolve(this.secondNavs);
            }else {
                let doResponse=this.doResponse();
                doResponse.then(()=>resolve(this.secondNavs));
            }
        }))
    }
});
export default NavStore



