require('normalize.css/normalize.css');
require('styles/App.css');
import React from 'react';
import Loadable from 'react-loadable';
import Loading from './common/moduleLoading';
import sizeStore from './index/sizeStore';
import sizeAction from './index/sizeAction';
import NavStore from './index/navStore';
class FixedNav extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            active: 0
        }
    }

    handleClick(nav) {
        this.props.event('secondClick', nav);
        return true
    }

    render() {
        return (
            <ul className="ul_header_nav">
                {
                    this.props.navs.map((nav) => nav.child.map((navc, indexc) => navc.fixed && (
                        <li key={indexc} className={navc.active && 'active' || ''}
                            onClick={this.handleClick.bind(this, navc)}><i
                            onClick={(event) => {
                                this.props.event('close', navc);
                                event.preventDefault();
                                event.stopPropagation()
                            }} className="iconfont">&#xe6d0;</i>{navc.pmisName}</li>) || ''))
                }
            </ul>
        )
    }
}

class Header extends React.Component {
    handleClick() {

    }

    render() {
        return (
            <div className="header">
                <div className="container_wrap">
                    <div className="container_vas">
                        <div className="header_title_bg"/>
                        <div className="header_title">   </div>
                        <FixedNav event={this.props.event} navs={this.props.navs}/>
                        <div className="header_top_a">
                            <ul>
                                <li><i className="iconfont">&#xe602;</i>
                                    <span>{this.props.userName}</span>
                                </li>
                                <li><i className="iconfont">&#xe60b;</i></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

class Left extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            active: null,
            style: sizeStore.getLeftBoxCss()
        }
    }

    handleClick(index) {
        let active = null;
        if (this.state.active !== index) {
            active = index;
        }
        this.setState({
            active: active
        });
        return true
    }

    secondClick(type, options) {
        this.props.event(type, options);
        return true
    }

    swtichWidth() {
        sizeAction.leftBoxWidthSwtich();
    }

    componentDidMount() {
        sizeStore.addListener('LEFT_BOX_WIDTH_SWTICH', this._onChange.bind(this));
    }

    componentWillUnmount() {
        sizeStore.remove('LEFT_BOX_WIDTH_SWTICH', this._onChange.bind(this));
    }

    _onChange() {
        this.setState({
            style: sizeStore.getLeftBoxCss(),
            mini: sizeStore.getLeftBoxState()
        })
    }

    render() {
        return (
            <div className={'nav_secondB fl ' + (this.state.mini ? 'mini' : '')} style={this.state.style}>
                <div onClick={this.swtichWidth.bind(this)} className="text-right controlbtn-wrap"><i
                    className="iconfont rotate180 controlbtn">&#xe60c;</i></div>
                <div className="unFade nav_secondB_default">
                    <ul>
                        {
                            this.props.navs.map((val, index) => {
                                return (
                                    <li onClick={this.handleClick.bind(this, index)} key={index}
                                        className={'templateBNav transitionBGColor ' + (this.state.active === index && 'active' || '')}>
                                        <div className="clearfix">
                                            <div className="fl"><i className="iconfont">&#xe608;</i></div>
                                            <div className="fl">{val.pmisName}</div>
                                            <div className="iconfont fr transitionTransform">&#xe60c;</div>
                                        </div>
                                        <ul className="transitionAll" key={index}>
                                            {
                                                val.child.map((cval, cindex) => <li onClick={(event) => {
                                                    this.secondClick('secondClick', cval);
                                                    event.preventDefault();
                                                    event.stopPropagation()
                                                }} key={cindex}
                                                                                    className={'templateBSecondNav transitionBGColor ' + (cval.active && 'active' || '')}>{cval.pmisName}</li>)
                                            }
                                        </ul>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </div>
        )
    }
}

class Content extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            style: sizeStore.getContentBoxCss()
        }
    }

    componentDidMount() {
        sizeStore.addListener('LEFT_BOX_WIDTH_SWTICH', this._onChange.bind(this));
    }

    componentWillUnmount() {
        sizeStore.removeListener('LEFT_BOX_WIDTH_SWTICH', this._onChange.bind(this));
    }

    _onChange() {
        this.setState({
            style: sizeStore.getContentBoxCss()
        })
    }

    render() {
        return (
            <div style={this.state.style} className="content autoW autoX fr">
                <div className="container_wrap h100">
                    <ul id="ul_list_page_wrap" className="container_vas ul_list_page autoY h100">
                        {
                            this.props.navs.map((val) => {
                                return val.child.map((valc,indexc) => {
                                    if (valc.fixed) {
                                        if (valc.controller) {
                                            const ControllerName = valc.controller;
                                            return (<ControllerName info={valc}/>)
                                        } else {
                                            const CurrentController=valc.controller = Loadable({
                                                loader: () => new Promise((re)=>{
                                                    require.ensure([],require=>{
                                                        re(require('./order/advance'))
                                                    },'./order/advance')
                                                }),
                                                loading: Loading
                                            });
                                            return (<CurrentController key={indexc} info={valc}/>)
                                        }
                                    }
                                })
                            })
                        }
                    </ul>
                </div>
            </div>
        )
    }
}

class VASController extends React.Component {
    constructor(props) {
        super(props);
        this.state = {navs: []};
        this.data = {navs: []};
    }

    componentDidMount() {
        let navs = null;
        NavStore.getNav().then((_nav) => {
            navs = _nav;
            return NavStore.getSecondNavs()
        }).then((response) => {
            this.data.navs = navs;
            this.data.secondNavs = response;
            this.setState({
                navs: navs,
                secondNavs: response
            });
        })
    }

    childEvent(type, option) {
        switch (type) {
            case 'close':
                this.secondClose(option);
                break;
            case 'secondClick':
                if (option) {
                    if (option.moduleName) {
                        this.secondClick(option);
                    }
                }
                break;
            default:
                break
        }
    }

    secondClose(moduleInfo) {

        if (!moduleInfo) return false;
        const fixed = [];
        for (let i = 0; i < this.data.secondNavs.length; i++) {
            if (this.data.secondNavs[i].fixed) {
                if (this.data.secondNavs[i].pmisName == moduleInfo.pmisName) {
                    if (!moduleInfo.active) {
                        moduleInfo.fixed = false;
                        this.setState({
                            nav: this.state.navs
                        });
                        return true;
                    } else {
                        moduleInfo.active = false;
                        moduleInfo.fixed = false;
                        if (fixed.length > 0) {
                            fixed[fixed.length - 1].active = true;
                            fixed[fixed.length - 1].fixed = true;
                            this.setState({
                                nav: this.state.navs
                            });
                            return true
                        }
                    }
                } else {
                    fixed.push(this.data.secondNavs[i]);
                }
            }
        }
        if (fixed.length > 0) {
            fixed[fixed.length - 1].active = true;
            fixed[fixed.length - 1].fixed = true;
        }
        this.setState({
            nav: this.state.navs
        });
        return true
    }

    secondClick(moduleInfo) {
        if (!moduleInfo) return false;
        const moduleName = moduleInfo.pmisName;
        for (let currentModule of this.data.secondNavs) {
            currentModule.active = currentModule.pmisName == moduleName;
            if (currentModule.pmisName == moduleName) {
                if (!currentModule.fixed) {
                    currentModule.fixed = true;
                }
            }
        }
        this.setState({navs: this.data.navs});
    }

    render() {
        return (
            <div>
                <Header navs={this.state.navs} event={this.childEvent.bind(this)}/>
                <Left navs={this.state.navs} event={this.childEvent.bind(this)}/>
                <Content navs={this.state.navs}/>
            </div>
        )
    }
}
